* {
  box-sizing: border-box;
}

a {
  margin-top: 10px;
  padding: 10px;
}

body {
  background-color: #ffffff;
  text-align: center;
  flex: 1;
  vertical-align: bottom;
}

.header1 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #f1f1f1;
}

.header2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: red;
}

.menu {
  margin-top: 10px;
  /* float: left; */
  /* width: 20%; */
}

.menuitem {
  padding: 8px;
  margin-top: 7px;
  border-bottom: 1px solid #f1f1f1;
}

.main {
  flex: 1;
/* justify-content: center; */
}

.main .top {
  flex: 1
}

.right {
  background-color: lightblue;
  float: left;
  width: 20%;
  padding: 10px 15px;
  margin-top: 7px;
}

.main .btn {
  background-color: #f1f1f1;
  color: black;
  font-size: 16px;
  margin: 5px;
  padding: 10px 30px;
  width: 220px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  text-align: center;
}

.main .btm .btn2 {
  background-color: red;
  color: floralwhite;
  font-size: 16px;
  margin: 5px;
  padding: 10px 30px;
  width: 220px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  text-align: center;
}

.main .btm {
  position: 'absolute';
  bottom: 0;
}

[class*="col-"] {
  float: left;
  padding: 15px;
  border: 1px solid lightblue;
}

.col-1 {
  width: 50%;
}

.col-2 {
  width: 50%;
  background-color: #ffffff;
}

@media only screen and (max-width:800px) {

  /* For tablets: */
  .main {
    width: 80%;
    padding: 0;
  }

  .right {
    width: 100%;
  }
}

@media only screen and (max-width:500px) {

  /* For mobile phones: */
  .menu,
  .main,
  .right {
    width: 100%;
  }
}